.volt-button{
color: #FFFFFF;
background:#003366;
border-radius:16px;
font-weight: bold;
padding:8px;
min-height:80px;
width:168px;
font-size: 16px;
/***font-size: 1.6vmin;*/
text-align:center;
/***line-height: 32px;*/
margin-left: 6px;
margin-right: 6px;
opacity: 0.9;
cursor: pointer;
}

.volt-button:active{
  opacity: 1;
transform: translateY(1px);
}

.volt-button.demo, .volt-button.demo:active{
  cursor: not-allowed;
  opacity: 0.9;
  transform: translateY(0px);
}



